.container {
  width: 100%;
  margin: 0 auto;
}

.photo_titre img {
  width: 100%;
  position: relative;
}

.textcenter {
  text-align: center;
}

.menu_icones {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  justify-content: center;
}

.line_block {
  display: inline-block;
}

.bg_grey {
  background-color: #f6fefe;
}

.icone {
  margin: 0 auto;
  text-align: center;
}

.icone img {
  border-radius: 50%;
}

.top_inner {
  position: relative;
}

button {
  position: absolute;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
  background-color: #f6fefe;
  color: black;
  border: 1px solid grey;
  padding: 5px;
}

@media screen and (min-width: 668px) {
  .gym {
    order: -1;
  }
}
